@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
  background-color: azure;
  background-image: url(/Weather-App/static/media/weatherbackground.9edece84.jpg);
  font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
}
.wrapper {

    padding: 2rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
   
}

.heading {
    margin-top: 0%;
    margin-bottom: 0%;
    color: rgb(82, 41, 33);
}

.search-area {
    display: -webkit-flex;
    display: flex;
    padding: .5rem;
    margin: .5rem;
}

.input-city {
    padding: .5rem;
    width: 20rem;
    margin: 1rem;
    border: none;
    border-radius: 0.5rem;
}

.btn-city {
    padding: .5rem;
    width: 5rem;
    margin: 1rem;
    border-radius: 0.5rem;
    border: none;
    transition: 300ms;
    background-color: rgb(137, 162, 214);
    font-weight: bold;
}

.btn-city:hover {
    background-color: rgb(81, 126, 221);
    color: white;
    cursor: pointer;
}

.weather {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

}

.city-name {
    font-size: 2rem;
    color: rgb(90, 45, 150);
    margin: .2rem;
}

.city-temp {
    margin: 0%;
    color: rgb(105, 41, 54);
}

.wind-speed {
    display: -webkit-flex;
    display: flex;

    font-size: .75rem;
    color: darkblue;
    padding: .25rem;

}

.city-description,
.city-wind {
    padding-left: 1rem;
    padding-right: 1rem;
    font-weight: bold;

}

.city-description {
    text-transform: capitalize;
}



.chart-temp {
   
    box-shadow: -4px 7px 5px 0px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: -4px 7px 5px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: -4px 7px 5px 0px rgba(0, 0, 0, 0.75);
    margin: 1rem;
}

.chart-humidity {
    box-shadow: 6px 7px 5px 0px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 6px 7px 5px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 6px 7px 5px 0px rgba(0, 0, 0, 0.75);
    margin: 1rem;
}
